import React, { createContext, useReducer, ReactNode, useContext } from "react";

interface TrackerState {
  studentName: string;
  subjects: string[];
  checkboxes: boolean[][];
  lessonSubject1: string;
  lessonSubject2: string;
  lessonSubject3: string;
  lessonSubject4: string;
  lessonGrid1: string[];
  lessonGrid2: string[];
  lessonGrid3: string[];
  lessonGrid4: string[];
  headerFont: string;
  fixedFont: string;
  dayFont: string;
  textBoxFont: string;
  textBoxFontSize: number;
  textBoxAlignment: "left" | "center" | "right";
  selectedSubject: string | null;
  showSettings: boolean;
  showResetConfirm: boolean;
  completedLessons: Record<string, string[][]>;
  dayNotes: Record<string, Record<string, string>>;
}

type TrackerAction =
  | { type: "SET_STUDENT_NAME"; payload: string }
  | { type: "SET_SUBJECTS"; payload: string[] }
  | { type: "SET_CHECKBOXES"; payload: boolean[][] }
  | { type: "SET_LESSON_SUBJECT"; payload: { index: number; value: string } }
  | { type: "SET_LESSON_GRID"; payload: { index: number; value: string[] } }
  | { type: "SET_FONT"; payload: { type: "header" | "fixed" | "day" | "textBox"; value: string } }
  | { type: "SET_TEXTBOX_STYLE"; payload: { type: "size" | "alignment"; value: number | string } }
  | { type: "SET_SELECTED_SUBJECT"; payload: string | null }
  | { type: "TOGGLE_SETTINGS" }
  | { type: "TOGGLE_RESET_CONFIRM" }
  | { type: "RESET_ALL" }
  | { type: "SET_DAY_NOTES"; payload: { date: string; key: string; notes: string } }
  | { type: "SET_COMPLETED_LESSONS"; payload: Record<string, string[][]> }; // New action

const initialState: TrackerState = {
  studentName: "",
  subjects: ["Subject 1", "Subject 2"],
  checkboxes: Array(2).fill(null).map(() => Array(31).fill(false)),
  lessonSubject1: "",
  lessonSubject2: "",
  lessonSubject3: "",
  lessonSubject4: "",
  lessonGrid1: Array(16).fill(""),
  lessonGrid2: Array(16).fill(""),
  lessonGrid3: Array(16).fill(""),
  lessonGrid4: Array(16).fill(""),
  headerFont: "Sacramento",
  fixedFont: "Sacramento",
  dayFont: "Sacramento",
  textBoxFont: "Sacramento",
  textBoxFontSize: 16,
  textBoxAlignment: "left",
  selectedSubject: null,
  showSettings: false,
  showResetConfirm: false,
  completedLessons: {},
  dayNotes: JSON.parse(localStorage.getItem("dayNotes") || "{}"),
};

const trackerReducer = (state: TrackerState, action: TrackerAction): TrackerState => {
  switch (action.type) {
    case "SET_STUDENT_NAME":
      return { ...state, studentName: action.payload };
    case "SET_SUBJECTS":
      return { ...state, subjects: action.payload };
    case "SET_CHECKBOXES":
      return { ...state, checkboxes: action.payload };
    case "SET_LESSON_SUBJECT":
      return {
        ...state,
        [`lessonSubject${action.payload.index}`]: action.payload.value,
      };
    case "SET_LESSON_GRID":
      return {
        ...state,
        [`lessonGrid${action.payload.index}`]: action.payload.value,
      };
    case "SET_FONT":
      return {
        ...state,
        [action.payload.type + "Font"]: action.payload.value,
      };
    case "SET_TEXTBOX_STYLE":
      return {
        ...state,
        textBoxFontSize: action.payload.type === "size" ? (action.payload.value as number) : state.textBoxFontSize,
        textBoxAlignment:
          action.payload.type === "alignment" ? (action.payload.value as "left" | "center" | "right") : state.textBoxAlignment,
      };
    case "SET_SELECTED_SUBJECT":
      return { ...state, selectedSubject: action.payload };
    case "TOGGLE_SETTINGS":
      return { ...state, showSettings: !state.showSettings };
    case "TOGGLE_RESET_CONFIRM":
      return { ...state, showResetConfirm: !state.showResetConfirm };
    case "RESET_ALL":
      return initialState;
    case "SET_DAY_NOTES":
      const { date, key, notes } = action.payload;
      const updatedNotes = {
        ...state.dayNotes,
        [date]: { ...(state.dayNotes[date] || {}), [key]: notes },
      };
      localStorage.setItem("dayNotes", JSON.stringify(updatedNotes));
      return { ...state, dayNotes: updatedNotes };
    case "SET_COMPLETED_LESSONS":
      return { ...state, completedLessons: action.payload };
    default:
      return state;
  }
};

const TrackerContext = createContext<{
  state: TrackerState;
  dispatch: React.Dispatch<TrackerAction>;
} | undefined>(undefined);

export const TrackerProvider: React.FC<{ children: ReactNode }> = ({ children }) => {
  const [state, dispatch] = useReducer(trackerReducer, initialState);
  return <TrackerContext.Provider value={{ state, dispatch }}>{children}</TrackerContext.Provider>;
};

export const useTracker = (): { state: TrackerState; dispatch: React.Dispatch<TrackerAction> } => {
  const context = useContext(TrackerContext);
  if (!context) {
    throw new Error("useTracker must be used within a TrackerProvider");
  }
  return context;
};